import React, { useEffect, useState } from "react";
import {
  CheckboxDiv,
  EditDelete,
  ListCheckbox,
  ListContent,
  ListDate,
  ListDiv,
  ListEditButton,
  ListImg,
  ListMain,
  ListText,
  ListTitle,
} from "../styles/mainliststyle";
import MenuTab from "./MenuTab";
import { deleteTodo } from "../api/todo/todo_api";
import CreateEdit from "./CreateEdit";
// {jsonData.title}

const MainList = props => {
  const item = props.item;
  const loginedUserId = props.loginedUserId;
  //console.log("item", item);
  // check   ListWrap  
  const [isChecked, setChecked] = useState(false);
  //    
  useEffect(() => {
    //  
    if (item.isChecked) {
      setChecked(true);
    }
  }, []);

  const handleCheckboxChange = () => {
    setChecked(!isChecked);
  };

  // 

  // isPopupOpen:   /   
  // setPopupOpen: isPopupOpen   
  const [isPopupOpen, setPopupOpen] = useState();

  const handlePopupToggle = () => {
    // setPopupOpen   isPopupOpen  
    setPopupOpen(!isPopupOpen);
  };

  const deleteTodoResultAction = obj => {
    const result = parseInt(obj.result);

    if (result === 0) {
      alert(" .");
    } else if (result === 1) {
      alert(" .");
      props.getTodoAllfn();
    } else if (result === 500) {
      alert(" . ");
    }
  };
  const handleClick = () => {
    //  todo    PK
    // 	 todo PK
    deleteTodo(loginedUserId, item.todoId, deleteTodoResultAction);
  };

  return (
    <ListMain backgroundColor={isChecked ? "#E4E2E6" : "#fff"}>
      <ListContent>
        <ListDiv>
          <ListImg>
            <img
              src={process.env.PUBLIC_URL + "/assets/images/bt_calendar.svg"}
            />
          </ListImg>
          <ListText>
            <ListTitle>{item.cleaning}</ListTitle>
          </ListText>
        </ListDiv>
        <ListDiv>
          <ListDate>{item.doDay}</ListDate>
          <CheckboxDiv>
            <ListCheckbox
              type="checkbox"
              checked={isChecked}
              onChange={handleCheckboxChange}
            ></ListCheckbox>
          </CheckboxDiv>
          <ListEditButton onClick={handlePopupToggle}>
            <img src="../assets/images/bt_scissors.svg" />
            {isPopupOpen && (
              <EditDelete>
                {/*    */}

                <MenuTab
                  type={1}
                  handleButtonClick={props.handleButtonClick}
                  handleButtonClickEdit={props.handleButtonClickEdit}
                  handleClick={handleClick}
                />

                {/*  */}
                <div onClick={handlePopupToggle}></div>
              </EditDelete>
            )}
          </ListEditButton>
        </ListDiv>
      </ListContent>
    </ListMain>
  );
};

export default MainList;
