export const postTodo = async (obj, postSuccess, postFail) => {
  console.log(".");
  try {
    const res = await axios.post(`api/todo`, obj);
    console.log(res.data);

    //       

    const resStatus = res.status.toString();
    // 
    if (resStatus.charAt(0) === "2") {
      console.log("");
      postSuccess(res.data);
    } else {
      //    :      
      //    :          
      // 400 :         
      // 403 :       .         . 401       .
      alert("  .");
    }
  } catch (error) {
    console.log(error);
    postFail();
  }
};

