import styled from "@emotion/styled";
import React, { useState } from "react";
import {
  MenuTabDelete,
  MenuTabEdit,
  MenuTabStyle,
} from "../styles/menutabstyle";
import { Divider } from "antd";
import { useNavigate } from "react-router-dom";
import CreateEdit from "./CreateEdit";

const MenuTab = props => {
  const item = props.item;
  const navigate = useNavigate();
  const [isPopupOpen, setPopupOpen] = useState(false);
  const handlePopupToggle = () => {
    // setPopupOpen   isPopupOpen  
    setPopupOpen(true);
  };

  const handleClickEdit = () => {
    //  item  .
    console.log("", item);
    //  item diaryId  navigate->    
    navigate(`/edit/${item.diaryId}`);
  };

  const pk = props.pk;
  const handleClick = props.handleClick;
  const handleButtonClick = props.handleButtonClick;

  return (
    <MenuTabStyle>
      {props.type === 1 ? (
        <>
          <MenuTabEdit onClick={props.handleButtonClickEdit}>
            <h3></h3>
          </MenuTabEdit>
        </>
      ) : (
        <>
          <MenuTabEdit onClick={handleClickEdit} item={item}>
            <h3></h3>
          </MenuTabEdit>
        </>
      )}
      {/* <MenuTabEdit onClick={handleClickEdit}>
        <h3></h3>
      </MenuTabEdit> */}
      <MenuTabDelete
        onClick={() => {
          handleClick(pk);
        }}
      >
        <h3></h3>
      </MenuTabDelete>
    </MenuTabStyle>
  );
};

export default MenuTab;
