import React from "react";
import {
  ErrorCodeNum,
  ErrorCodeNumDiv,
  ErrorCodeSpan,
  ErrorDocsDiv,
  ErrorDocsText,
  ErrorDocsTitle,
  ErrorPageDiv,
  ErrorPageMain,
  GoMainBt,
  GoMainBtDiv,
  GoMainBtP,
} from "../styles/errorpagestyle";
import { useNavigate } from "react-router";

const ErrorPage = () => {
  const navigate = useNavigate();

  const goToMainPage = () => {
    //     
    navigate("/");
  };
  return (
    <ErrorPageMain>
      <ErrorPageDiv>
        <ErrorCodeNumDiv>
          <ErrorCodeSpan>ERROR CODE : </ErrorCodeSpan>
          <ErrorCodeNum>404</ErrorCodeNum>
        </ErrorCodeNumDiv>
        <ErrorDocsDiv>
          <ErrorDocsTitle>    .</ErrorDocsTitle>
          <ErrorDocsText>
               .     
            !
          </ErrorDocsText>
        </ErrorDocsDiv>
        <GoMainBtDiv>
          <GoMainBt onClick={goToMainPage}>
            <GoMainBtP> </GoMainBtP>
          </GoMainBt>
        </GoMainBtDiv>
      </ErrorPageDiv>
    </ErrorPageMain>
  );
};

export default ErrorPage;
