import React, { useEffect, useState } from "react";
import {
  CreateList,
  CreateListBg,
  CreateTitle,
} from "../styles/createeditstyle";

import { postTodo, fetchTodo } from "../api/todo/todo_api";
import moment from "moment/moment";

const CreateAdd = ({ text, handleCancel, cleanListUpdate }) => {
  const [title, setTitle] = useState("");
  const [cleaning, setCleaning] = useState("");
  const [doDay, setDoDay] = useState("");
  const [inputNumber, setInputNumber] = useState("");

  const handleInputChange = e => {
    const value = e.target.value;

    //       
    if (!isNaN(value) || value === "") {
      //      
      const numericValue = value.replace(/[^0-9]/g, "");

      // 8    
      const trimmedValue = numericValue.slice(0, 8);

      //      
      const formatted = trimmedValue.replace(
        /(\d{4})(\d{2})(\d{2})/,
        "$1-$2-$3",
      );

      setInputNumber(formatted);
    }
  };
  const handleClearTitle = e => {
    e.preventDefault();
    setTitle("");
  };

  const postSuccess = () => {
    alert(" ");

    cleanListUpdate();
    handleCancel(true);
  };
  const postFail = () => {
    alert(" .  .");
    handleCancel(false);
  };
  const diaryPost = () => {
    const obj = {
      loginedUserId: 2,
      cleaning: cleaning,
      doDay: doDay,
    };
    // console.log("  ", obj);
    postTodo(obj, postSuccess, postFail);
  };

  const diaryFetch = () => {
    const obj = {
      loginedUserId: 2,
      todoId: 0,
      cleaning: "string",
      doDay: "string",
    };
  };

  const handleConfirm = e => {
    if (cleaning === "") {
      alert(" .");
      return;
    }
    if (doDay === "") {
      alert(" .");
      return;
    }
    diaryPost();
  };

  //  
  const handleChangeTitle = e => {
    setCleaning(e.target.value);
  };
  //    
  const handleChangeDate = e => {
    setDoDay(e.target.value);
  };

  useEffect(() => {
    const now = moment();
    setDoDay(now.format("YYYY-MM-DD"));
  }, []);

  return (
    <CreateListBg>
      <CreateList>
        <div className="create-header">
          <div>
            <CreateTitle> {text}</CreateTitle>
          </div>
          <img
            className="create-logo"
            src={process.env.PUBLIC_URL + "./assets/images/logo.svg"}
            alt=""
          />
        </div>
        <div className="create-main">
          <div className="create-main-top">
            <div className="create-main-left">
              <img src="./assets/images/icon.svg" alt="" />
            </div>
            <div className="create-main-right">
              <div className="create-date">
                <input
                  type="date"
                  placeholder="yyyy-mm-dd"
                  maxLength="10"
                  value={doDay}
                  onChange={handleChangeDate}
                  // value={inputNumber}
                  // onChange={handleInputChange}
                />
              </div>
              <h2>  !</h2>
            </div>
          </div>

          <form className="create-main-ft">
            <input
              className="create-main-txt"
              type="text"
              name="title"
              value={cleaning}
              maxLength={50}
              placeholder="  ."
              onChange={handleChangeTitle}
            />

            <input
              type="button"
              onClick={handleClearTitle}
              className="create-main-bt"
            />
          </form>
        </div>
        <div className="create-footer">
          <div className="create-footer">
            <button
              className="bt-no"
              onClick={() => {
                handleCancel();
              }}
            >
               
            </button>

            <button className="bt-yes" type="submit" onClick={handleConfirm}>
               
            </button>
          </div>
        </div>
      </CreateList>
    </CreateListBg>
  );
};

export default CreateAdd;
