import React, { useEffect, useState } from "react";
import {
  DiaryAddFooter,
  DiaryAddHeader,
  DiaryAddHeaderComment,
  DiaryAddMain,
  DiaryAddMainButton,
  DiaryAddMainContent,
  DiaryAddMainImage,
  DiaryAddMainTitle,
  DiaryAddStyle,
} from "../styles/diaryaddstyle";
import { getDiaryId, putDiary } from "../api/diary/diary_api";
import { Navigate, useNavigate, useParams } from "react-router-dom";
import Confirm from "./Confirm";

// ==============================================================
//  ?
const obj = {
  loginedUserId: 0,
  diaryId: 0,
  nickname: "string",
  title: "string",
  contents: "string",
  pics: ["string"],
  createdAt: "string",
};
// ==============================================================

const DiaryEdit = props => {
  const [diaryId, setDiaryId] = useState("2");
  const [diaryEditList, setEditDiaryList] = useState([]);
  // useEffect(() => {
  //   getDiaryId(diaryId, setEditDiaryList);
  // }, []);
  //   const params = useParams();
  //   const diaryId = params.diaryId;

  //   console.log(" ID", diaryId);
  const [loginedUserId, setLoginedUserId] = useState("2");
  //   const [page, setPage] = useState(1);
  //   const [diaryList, setDiaryList] = useState([]);
  const [nowDiary, setNowDiary] = useState({});

  useEffect(() => {
    getDiaryId(diaryId, setEditDiaryList);
  }, []);
  const findeDiary = () => {
    setNowDiary();
  };

  const navigate = useNavigate();
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [pics, setPics] = useState([]);

  // ==============================================================
  const [uploadImgBefore, setUploadImgBefore] = useState(
    `${process.env.PUBLIC_URL}/assets/images/bt_media.svg`,
  );
  const [uploadImgAfter, setUploadImgAfter] = useState(
    `${process.env.PUBLIC_URL}/assets/images/bt_media.svg`,
  );
  // ==============================================================

  useEffect(() => {
    // uploadImgBefore  uploadImgAfter    .
  }, [uploadImgBefore, uploadImgAfter]);
  const handleChange = (event, type) => {
    const file = event.target.files[0];
    const reader = new FileReader();
    reader.onload = () => {
      const imageUrl = reader.result;
      //      
      if (type === "before") {
        setUploadImgBefore(imageUrl);
      } else if (type === "after") {
        setUploadImgAfter(imageUrl);
      }
    };
    if (file) {
      reader.readAsDataURL(file); //   URL 
    }
  };

  const [confirmOpen, setConfirmOpen] = useState(false);
  const handleButtonClick = () => {
    setConfirmOpen(true);
  };
  const handleConfirm = async e => {
    if (nowDiary.title === "") {
      setConfirmOpen(false);
      alert(" .");
      return;
    }
    if (nowDiary.content === "") {
      setConfirmOpen(false);
      alert(" .");
      return;
    }
    // const obj = {
    //   loginedUserId: 3,
    //   title: title,
    //   contents: content,
    //   pics: [
    //     "https://upload.wikimedia.org/wikipedia/commons/thumb/a/a2/NewJeans_X_OLENS_1_%28cropped%29.jpg/250px-NewJeans_X_OLENS_1_%28cropped%29.jpg",
    //     "",
    //   ],
    // };
    putDiary(obj, putSuccess, putFail);
    // PUT  
    try {
      await putDiary(nowDiary); //     
      navigate(`/diary`);
    } catch (error) {
      console.error("Error updating diary:", error);
      alert(" .  .");
    }

    setConfirmOpen(false);
  };
  const putSuccess = () => {
    navigate(`/diary`);
  };
  const putFail = () => {
    alert(" .  .");
    setConfirmOpen(false);
  };
  const handleCancel = () => {
    setConfirmOpen(false);
  };

  // ==============================================================
  //  
  const handleChangeTitle = e => {
    setEditDiaryList(prevDiary => ({
      ...prevDiary,
      title: e.target.value,
    }));
  };
  //    
  const handleChangeContent = e => {
    setNowDiary(prevDiary => ({
      ...prevDiary,
      contents: e.target.value,
    }));
  };
  const handleClearTitle = () => {
    //    
    setNowDiary(prevDiary => ({
      ...prevDiary,
      title: "",
    }));
    console.log("Title cleared");
  };
  // ==============================================================
  return (
    <DiaryAddStyle>
      <DiaryAddHeader>
        <DiaryAddHeaderComment>
          <h1>!</h1>
          <span>
             <b></b>  
          </span>
          <span>
            <b></b> .
          </span>
        </DiaryAddHeaderComment>
        <img
          src={process.env.PUBLIC_URL + "../assets/images/logo.svg"}
          alt=""
        />
      </DiaryAddHeader>
      {diaryEditList.map(item => (
        <DiaryAddMain key={item.diaryId}>
          <DiaryAddMainTitle>
            <form>
              <input
                type="text"
                maxLength={50}
                placeholder="  "
                name="title"
                value={item.title}
                className="diaryadd-title"
                onChange={handleChangeTitle}
              />
              <input
                type="button"
                className="diaryadd-title-bt"
                onClick={handleClearTitle}
              />
            </form>
          </DiaryAddMainTitle>
          <DiaryAddMainContent>
            <textarea
              type="text"
              maxLength={2000}
              value={item.contents}
              onChange={handleChangeContent}
              placeholder="  ."
              name="content"
            />
          </DiaryAddMainContent>
          <DiaryAddMainImage>
            <img src={item.pics} alt="" className="diaryadd-img-before" />
            <img src={item.pics} alt="" className="diaryadd-img-after" />
          </DiaryAddMainImage>
          <DiaryAddMainButton>
            <label htmlFor="input-file-before">
              <button
                type="button"
                onClick={() => {
                  document.getElementById("input-file-before").click();
                }}
                className="diaryadd-img-input-button-before"
              >
                   
              </button>
            </label>
            <input
              type="file"
              accept="image/png, image/gif, image/jpeg"
              onChange={event => {
                handleChange(event, "before");
              }}
              id="input-file-before"
              style={{ display: "none" }}
            />
            <label htmlFor="input-file-after">
              <button
                type="button"
                onClick={() => {
                  document.getElementById("input-file-after").click();
                }}
                className="diaryadd-img-input-button-after"
              >
                   
              </button>
            </label>
            <input
              type="file"
              accept="image/png, image/gif, image/jpeg"
              onChange={event => {
                handleChange(event, "after");
              }}
              id="input-file-after"
              style={{ display: "none" }}
            />
          </DiaryAddMainButton>
        </DiaryAddMain>
      ))}
      <DiaryAddFooter>
        <button onClick={handleButtonClick}>
          <img
            src={process.env.PUBLIC_URL + "/assets/images/bt_checkicon.svg"}
          />
          
        </button>
        {confirmOpen && (
          <Confirm
            type={1}
            txt={
              <div>
                <span></span> ?
              </div>
            }
            onOk={handleConfirm}
            onNo={handleCancel}
          />
        )}
      </DiaryAddFooter>
    </DiaryAddStyle>
  );
};
export default DiaryEdit;
