import styled from "@emotion/styled";
import React, { useEffect } from "react";
import { useHistory, useNavigate } from "react-router-dom"; // react-router-dom useHistory 

const IntroPage = () => {
  const navigate = useNavigate(); // useHistory   history  

  useEffect(() => {
    //    2    
    const timer = setTimeout(() => {
      // history     
      navigate("/main");
    }, 2000);

    //       
    return () => clearTimeout(timer);
  }, [navigate]); // history  

  const IntroBack = styled.div`
    width: 1024px;
    height: 1366px;
    background-color: #fff;
    position: relative;
  `;
  const IntroMain = styled.div`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  `;
  const IntroLogo = styled.div`
    margin-bottom: 100px;
  `;
  const IntroText = styled.span`
    font-size: 20px;
    text-align: center;
    font-style: normal;
    color: #176bff;
    opacity: 0.5;
    font-weight: 500;
    line-height: 1.5;
  `;
  return (
    <IntroBack>
      <IntroMain>
        <IntroLogo>
          <img
            src={process.env.PUBLIC_URL + "/assets/images/intro_logo.svg"}
            alt="intrologo"
          />
        </IntroLogo>
        <IntroText>
          <p>
                  .  
            <br />
                      .
            <br />
                   . <br />- <br />
             
          </p>
        </IntroText>
      </IntroMain>
    </IntroBack>
  );
};

export default IntroPage;
