import React from "react";
import {
  ArrowButton,
  HiUser,
  HiUserDiv,
  SubBarApp,
  SubBarAppP,
  SubBarBody,
  SubBarDiv,
  SubBarDocs,
  SubBarLogo,
  SubBarLogoImg,
  SubBarPlan,
  SubBarSayHi,
  SubBarTitle,
  SubBarWrite,
  UserName,
} from "../styles/subbarstyle";
// {jsonData.nickName}
const SubBar = (props) => {
  const nickname = props.nickname;
  return (
    <SubBarBody>
      <SubBarDiv>
        <HiUser>
          <HiUserDiv>
            <SubBarSayHi></SubBarSayHi>
            <UserName>{nickname}</UserName>
          </HiUserDiv>
          <div>
            <ArrowButton></ArrowButton>
          </div>
        </HiUser>
        <SubBarApp>
          <SubBarAppP>[     ]</SubBarAppP>
          <SubBarTitle>  </SubBarTitle>
          <SubBarDocs>
                      
                .
          </SubBarDocs>
        </SubBarApp>
        <SubBarPlan>
          <SubBarTitle>   !</SubBarTitle>
          <SubBarDocs>
                    .
          </SubBarDocs>
        </SubBarPlan>
        <SubBarWrite>
          <SubBarTitle>   !</SubBarTitle>
          <SubBarDocs>
              ,      . 
                 .
          </SubBarDocs>
        </SubBarWrite>
        <SubBarLogo>
          <SubBarLogoImg></SubBarLogoImg>
        </SubBarLogo>
      </SubBarDiv>
    </SubBarBody>
  );
};
export default SubBar;